.banner {
	color: white;
	object-fit: contain;
	height: 448px;
}

.banner__contents {
	margin-left: 60px;
	padding-top: 140px;
	height: 190px;
}

.banner__title {
	font-size: 3rem;
	font-weight: 800;
	padding-bottom: 0.3rem;
}

.banner__description {
	width: 45rem;
	line-height: 1.3;
	padding-top: 1rem;
	font-size: 1rem;
	max-width: 500px;
	height: 80px;
}

.banner__btn {
	cursor: pointer;
	color: #fff;
	outline: none;
	border: none;
	font-weight: 700;
	border-radius: 0.2vw;
	padding: 0.5rem 2rem;
	margin-right: 1.3rem;
	background-color: rgba(51, 51, 51, 0.5);
}

.banner__btn:hover {
	color: #000;
	background-color: #e6e6e6;
	transition: all 0.2s;
}

.banner__fadeBottom {
	height: 7.4rem;
	background-image: linear-gradient(
		180deg,
		transparent,
		rgba(37, 37, 37, 0.61),
        #111
	);
}
